<template>
  <div class="welindex">
    <el-container>
      <el-header>
        <div class="left">
          <img src="../assets/heima.png" alt="">
          <span>电商后台管理系统</span>
        </div>
        <el-button type="info" class="right" @click="loginout">退出</el-button>
      </el-header>
      <el-container>
        <el-aside :width="collapse ? '60px' : '200px'">
          <el-menu
            :default-active="defaultActive"
            class="el-menu-vertical-demo"
            unique-opened
            router
            :collapse-transition="false"
            :collapse="collapse"
            @select="pathbtn"
            background-color="#333744"
            text-color="#fff">
            <div class="top-bar" @click="collapse=!collapse">|||</div>
            <el-submenu v-for="item in arr" :key="item.id" :index="item.path">
              <template slot="title">
                <i :class="pathobj[item.id]"></i>
                <span>{{ item.authName }}</span>
              </template>
              <el-menu-item v-for="item2 in item.children" :key="item2.id" :index="'/'+item2.path"><i
                class="el-icon-menu"></i>{{ item2.authName }}
              </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>
        <el-main>
          <transition>
            <router-view></router-view>
          </transition>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
<script>
export default {
  created () {
    this.getMenus()
  },
  data () {
    return {
      arr: [],
      pathobj: {
        125: 'iconfont icon-users',
        103: 'iconfont icon-tijikongjian',
        101: 'iconfont icon-shangpin',
        102: 'iconfont icon-danju',
        145: 'iconfont icon-baobiao'
      },
      defaultActive: localStorage.getItem('path'),
      collapse: false
    }
  },
  methods: {
    async getMenus () {
      const res = await this.$http('/menus')
      this.arr = res.data.data
    },
    pathbtn (index) {
      this.defaultActive = index
      localStorage.setItem('path', index)
    },
    loginout () {
      this.$confirm('您确认退出登录吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        localStorage.removeItem('token')
        localStorage.removeItem('path')
        this.$router.push('/login')
      }).catch((err) => {
        return err
      })
    }
  }
}
</script>
<style lang="less" scoped>
.v-enter {
  transform: translateX(100%);
}

.v-enter-active {
  transition: all 2s;
}

.v-enter-to {
  transform: translateX(0);
}

.welindex {
  width: 100%;
  height: 100%;

  .el-container {
    height: 100%;
  }

  .el-header {
    background-color: #373d41;
    color: #fff;
    line-height: 60px;
    font-size: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .left {
      height: 100%;
      display: flex;
      align-items: center;

      span {
        margin-left: 15px;
      }
    }
  }

  .el-aside {
    background-color: #333744;
    color: #333;
    width: 100%;

    .iconfont {
      margin-right: 10px;
    }

    .el-menu {
      border: none;
      width: 100%;

      .top-bar {
        width: 100%;
        color: #fff;
        text-align: center;
        line-height: 22px;
        font-size: 12px;
        background-color: #4a5064;
        cursor: pointer;
      }

      .el-aside {
        .el-menu-vertical-demo {
          width: 100%;

          .el-submenu {
            width: 100%;

            .el-submenu__title {
              width: 100%;
            }
          }
        }
      }
    }
  }

  .el-main {
    background-color: #eaedf1;
    color: #333;
  }
}

</style>
